import { useState, useEffect } from "react";
import Layout from "../components/LayoutComponent";
import LastSummary from "../components/LastSummaryComponent";
import CombinedChart from "../components/CombinedChartComponent";
import SavingsPrediction from "../components/SavingsPredictionComponent";
import AddTransactionForm from "../components/AddTransactionForm";
import { Transaction } from "../interfaces/InterfaceData";

const Dashboard = () => {
  const [transactions, setTransactions] = useState<Transaction[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const savings = 500;

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        const userId = sessionStorage.getItem("userId"); // Get userId from session storage

        if (!userId) {
          setError("User ID is missing in session storage");
          return;
        }

        const response = await fetch(`/api/transaction?userId=${userId}`);

        if (!response.ok) {
          throw new Error("Gagal memuat data transaksi");
        }

        const data: Transaction[] = await response.json();
        console.log("Fetched Data:", data);
        setTransactions(data);
      } catch (error) {
        setError("Gagal memuat data!");
        console.error("Error fetching data:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  // Handle Add Transaction
  const handleAddTransaction = (
    newTransaction: Omit<Transaction, "userId">
  ) => {
    const userId = sessionStorage.getItem("userId"); // Get userId from session storage

    if (!userId) {
      console.error("User ID is missing in session storage");
      return;
    }

    const transactionWithUserId: Transaction = {
      ...newTransaction,
      userId, // Add userId to the new transaction
    };

    console.log("New Transaction Added:", transactionWithUserId); // Log added transaction
    setTransactions([...transactions, transactionWithUserId]);
  };

  // Separate income and expense data from transactions
  const incomeData = transactions.filter(
    (transaction) => transaction.type === "income"
  );
  const expenseData = transactions.filter(
    (transaction) => transaction.type === "expense"
  );

  return (
    <Layout>
      {loading && <p>Loading...</p>}
      {error && <p className="text-red-500">{error}</p>}

      <div className="bg-white shadow-md rounded-lg p-6 mb-8">
        <h3 className="text-2xl font-semibold mb-4">
          Grafik Pemasukan dan Pengeluaran
        </h3>
        {/* <CombinedChart incomeData={incomeData} expenseData={expenseData} /> */}
      </div>

      <div className="mb-8">
        <LastSummary />
      </div>

      <div className="bg-white shadow-md rounded-lg p-6 mb-8">
        <AddTransactionForm onAddTransaction={handleAddTransaction} />
      </div>

      <div className="bg-white shadow-md rounded-lg p-6 mb-8">
        <h3 className="text-2xl font-semibold mb-4">Prediksi Tabungan</h3>
        <SavingsPrediction savings={savings} />
      </div>
    </Layout>
  );
};

export default Dashboard;
